 <template>
  <div>
    <el-table :data="tableData" class="userIndex">
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column label="访问编号" width="80px">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{ scope.row.id }}</span>
        </template>
      </el-table-column>
      <el-table-column label="登录名称" width="160px">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{ scope.row.nickName }}</span>
        </template>
      </el-table-column>
      <el-table-column  label="登陆地点" width="170px">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{ scope.row.place }}</span>
        </template>
      </el-table-column>
      <el-table-column label="浏览器" width="160px">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{ scope.row.browser }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="time" label="登录时间" width="170px">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{ scope.row.loginTime }}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template> 


<script>
import {  getJournal } from "@/moke/getData";

export default {
  data() {
    return {
      state: false,
      tableData: [],
      labelPosition: "left",
      formLabelAlign: {},
      formLabelWidth: "120px",
      index:''
    };
  },
  methods: {
    async getMockList() {
      try {
        const result = await getJournal();
        // console.log(result);
        if (result.data.code == 0) {
          this.tableData = result.data.data.data;
        }
        console.log(result.data.data.data);
      } catch (error) {
        console.log(error);
      }
    },
  },
  mounted() {
    this.getMockList();
  },
};
</script>

